@import '../../scss/mixins';

.Portfolio {
    @include section;
    background: $themeGrey;
    padding-bottom: 170px;

    &-button {
        position: relative;
        top: 70px;
        background: $themeColour;
        border: none;
        padding: 10px 30px;
        border-radius: 5px;
        color: $themeGrey;
        transition: .2s;
        &:hover {
            background: white;
        }
    }

    &-line {
        height: 200px;

        .moved {
            align-self: flex-end;
            text-align: left;
        }

        .bordered {border-right: 7px solid $themeColour;}
        .hgt {height: 140px;}

        &--text {
            color: white;
            text-align: right;
            height: 155px;
            &-header {
                font-weight: 600;
                font-size: 18px;
                color: $themeColour;
            }
            &-icons {
                &-icon {
                    margin: 0 5px;
                    color: white;
                    font-size: 20px;
                    transition: .2s;
                    &:hover {
                        color: $themeColour;
                        cursor: pointer;
                    }
                }
            }
        }

        &--element {
            .thumbnail {
                background-size: contain !important;
                background-repeat: no-repeat !important;
                background-position: center !important;
                position: relative;
                height: 140px;
                transition: .2s ease-in;
                margin: 5px 0;
                &:hover {
                    cursor: pointer;
                }
                &-hover-left {
                    background: url('../../images/projects/monstera-left.png');
                }
                &-hover-right {
                    background: url('../../images/projects/monstera-right.png');
                }
                &-hover-left, &-hover-right {
                    top: 0;
                    left: 0;
                    background-size: contain;
                    background-position: center;
                    background-color: transparent;
                    position: absolute;
                    height: 100%;
                    width: 100%;
                    z-index: 3;
                    transition: .5s ease-in;
                    &:hover {
                        opacity: 0;
                    }
                }
            }
        }
    }

    .bg-1 {background: url('../../images/projects/project-1.png');}
    .bg-2 {background: url('../../images/projects/project2.png');}
    .bg-3 {background: url('../../images/projects/project-3.png');}
    .bg-4 {background: url('../../images/projects/project-4.png');}
    .bg-5 {background: url('../../images/projects/project5.png');}
    .bg-6 {background: url('../../images/projects/project6.png');}
    .bg-7 {background: url('../../images/projects/project7.png');}
    .bg-8 {background: url('../../images/projects/project8.png');}
    .bg-9 {background: url('../../images/projects/project9.png');}
    .bg-10 {background: url('../../images/projects/project10.png');}
    .bg-11 {background: url('../../images/projects/project11.png');}
    .bg-12 {background: url('../../images/projects/project12.png');}
    .bg-13 {background: url('../../images/projects/project13.png');}
    .bg-14 {background: url('../../images/projects/project14.png');}
    .bg-15 {background: url('../../images/projects/project15.png');}
}

@media (max-width: 991.98px) { 
    .Portfolio {
        &-button {
            top: 30px;
        }
        &-line {
            height: 400px;
            .moved {
                align-self: flex-end;
                text-align: left;
            }
            .bordered {
                border-right: none
            }
            .bordered-bot {
                border-bottom: 1px solid #454545;
                padding-bottom: 20px
            }
            .hgt {
                height: 200px;
            }
        }
    }
}

@media (max-width: 767.98px) { 
    .Portfolio-line--text {
        text-align: left;
    }
}

@media (max-width: 575.98px) { 
    .Portfolio {
        &-line {
            height: 660px;
            font-size: 0.9em;
            .hgt {min-height: 600px;}
            .moved {text-align: center;}
            &--text {
                text-align: center;
                color: white;
                height: auto;
                padding-top: 50px;
                &-header {
                    font-weight: 600;
                    font-size: 1.1em;
                    color: $themeColour;
                }
                &-icons {
                    margin-bottom: 10px;
                    &-icon {
                        margin: 0 5px;
                        color: white;
                        font-size: 20px;
                        transition: .2s;
                        &:hover {
                            color: $themeColour;
                            cursor: pointer;
                        }
                    }
                }
            }
            &--element {
                height: 90px;
                margin-top: 30px;
                &-hover-left, &-hover-right {
                    display: none;
                }
            }
        }
    }
}

@media (max-width: 340px) { 
    .Portfolio {
        &-line {
            height: 700px;
        }
    }
}